import React from 'react'
import Layout from '../components/layout'
import Seo from '../components/seo'

export default function About() {
  return (
    <Layout showSidebar={false}>
      <Seo title="about" />
      <header className="max-w-2xl mx-auto mb-4">
        <h1 className="text-3xl mb-4">About Me</h1>
      </header>
      <hr className="my-4" />
      <div className="max-w-2xl mx-auto">
        大家好，我叫
        Yury，程式是我工作的一部份，也是我的興趣。小時候接觸樂高機器人，用 GUI
        編寫控制邏輯的印象還記憶猶深，想必是那時的快樂深深影響了我，讓我最後踏上程式之路。
        <br />
        我喜歡思考如何做到一件事，拆解所需的因素、尋找實現的方法、分析不同的方案，最後透過程式來實作。
        為什麼最後是透過程式完成的呢？因為比起處理人的問題，寫程式直覺多了。
        而紀錄這個過程並分享給大家就是建立這個部落格的初衷，請多多指教😄。
      </div>
    </Layout>
  )
}
